* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: system-ui;
}

$MColor :rgb(225, 72, 45);
$BgColor: #333;
$MuColor: rgb(41, 41, 41);
$LightColor: rgb(248, 248, 248);
$DarkColor: #e0e0e0;
$Shadow: 0px 2px 3px rgba(0, 0, 0, 0.377);
$Radius: 5px;

// button style
@mixin Button {
    font-size: 1em;
    padding: 10px 20px;
    border-radius: $Radius;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    font-weight: 400;
    color: $DarkColor;
    cursor: pointer;
    box-shadow: $Shadow;
    position: relative;
    overflow: hidden;
    transition: 0.35s;
    transform-style: color;
    z-index: 2;

    &::before {
        content: "";
        position: absolute;
        top: 100%;
        right: -15%;
        width: 40px;
        height: 40px;
        transform: translateY(-50%);
        border-radius: 50%;
        background-color: $DarkColor;
        transition: 0.4s;
        z-index: -1;
    }

    &::after {
        content: "";
        position: absolute;
        top: 0%;
        right: 0%;
        width: 100%;
        height: 100%;
        background-color: $MColor;
        transition: 0.4s;
        z-index: -2;
    }

    &:hover {
        color: $MColor;

        &::before {
            content: "";
            position: absolute;
            top: 0%;
            right: 0%;
            width: 100% + 12%;
            height: 100% + 100%;
            border-radius: 0;
            background-color: $DarkColor;
            transition: 0.4s;

        }
    }
}

@mixin Mi($g) {
    @media (max-width:$g) {
        @content;
    }
}

body {
    background-color: $BgColor;
    font-size: 16px;
    position: relative;
    @include Mi(820px){
        font-size: 18px;
    }
}

section {
    padding: 20px 50px;
}

.navbar {
    width: 100%;
    padding: 20px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;

    justify-content: space-between;
    background-color: $MuColor;
    box-shadow: $Shadow;
    position: relative;

    @include Mi(570px){
        padding: 10px 50px;
    }

    .logo {
        align-items: center;
        h2 {
            font-size: 1.6em;
            color: $LightColor;
            align-items: center;
            span {
                color: $MColor;
            }
        }
    }

    nav {
        display: flex;
        gap: 10px;
        align-items: center;
        align-content: center;

        a {
            text-decoration: none;
            color: $LightColor;
            font-weight: 500;
            padding: 5px;
        }

        .Hr {
            background-color: $MColor;
            border-radius: 10px;
        }

        @include Mi(570px){
            display: block;
            width: 10em;
            background-color: $MuColor;
            border-radius: $Radius;
            position: fixed;
            box-shadow: $Shadow;
            top: 60px;
            right: -100%;
            margin: 5px;
            padding: 5px;
            transition: 0.4s;
            transform-style: right;

            a{
                width: 100%;
                display: inline-block;
                text-align: center;
            }
        }
       
    }
    .show{
            right: 0;
    }
    .naShow{
        display: block;
        width: 30px;
        height: 30px;
        padding: 5px;
        background-color: #222;
        border-radius: 50%;
        border: 2px solid #444;
        display: none;
        @include Mi(570px){
            display: block;
        }
    }
}

footer {
    width: 100%;
    padding: 25px;
    text-align: center;
    background-color: $MuColor;
    position: absolute;
    top: 105%;

    h2 {
        font-size: 1em;
        color: $DarkColor;

        span {
            color: $MColor;
            text-transform: uppercase;
        }
    }

    
}


.Home {
    background-size: cover;
    height: 80vh;
    transition: 0.5s;
    transform-style: background-image;
    margin-bottom: 80px;
    .content {
        padding: 150px 0;

        h2 {
            font-size: 2.3em;
            padding: 10px 0;
            color: $MColor;
            font-weight: 700;
            text-shadow: $Shadow;
        }

        p {
            font-size: 1.4em;
            padding: 5px 0;
            font-weight: 500;
            color: $DarkColor;
            max-width: 20em;
            text-shadow: $Shadow;
        }

        button {
            margin-top: 20px;
            @include Button;
            font-size: 1.2em;
        }
    }
}



.Pro {
    width: 100%;

    .crds {
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: row;
        flex-wrap: wrap;

        .crd {
            width: 15em;
            padding: 10px;
            background-color: $MuColor;
            border-radius: $Radius;
            box-shadow: $Shadow;
            margin: 10px;
            text-align: center;

            .cover {
                width: 100%;
                height: 100%;
                border: 1px solid #111;
                border-radius: $Radius;
                padding: 10px;

                img {
                    width: 100%;
                    padding: 0px;
                }

                .content {
                    h3 {
                        color: $MColor;
                        font-size: 1.4em;
                        text-transform: uppercase;
                        padding: 5px 0;
                    }

                    p {
                        font-size: 1em;
                        padding: 5px 0;
                        color: $DarkColor;
                        font-weight: 400;
                    }

                    h4 {
                        width: 100%;
                        padding: 5px 0;
                        display: inline-block;
                        font-weight: 400;
                        color: $DarkColor;

                        span {
                            color: $MColor;
                        }

                        font-size: 1.3em;
                    }

                    button {
                        width: 120px;
                        margin-top: 15px;
                        @include Button;
                    }
                }
            }
        }
    }
}

.srvs {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 100px 50px;

    .allser {
        width: 85%;
        padding: 20px;
        display: flex;
        justify-content: center;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 15px;
        background-color: $MuColor;
        border-radius: $Radius;
        box-shadow: $Shadow;
        align-items: center;


        .srCrd {
            width: 20em;
            height: 20em;
            text-align: center;
            padding: 2px;
            border: 1px solid #333;
            padding: 10px;
            align-items: center;

            img {
                width: 10em;
                margin: 5px 0;
            }

            h3 {
                font-size: 1.5em;
                color: $MColor;
                padding: 5px 0;
            }

            p {
                font-size: 1em;
                padding: 5px;
                color: $DarkColor;
            }

            button {
                margin-top: 15px;
                @include Button;
                box-shadow: none;
            }
        }
    }
}



.about {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 120px 20px;

    .bout {
        width: 22em;
        padding: 30px;
        margin: 5px;
        background-color: $MuColor;
        border-radius: $Radius;
        box-shadow: $Shadow;
        text-align: center;

        h3 {
            font-size: 1.5em;
            padding: 10px 0;
            color: $MColor;
        }

        p {
            padding: 5px 0;
            font-size: 1em;
            color: $DarkColor;
        }

        .info {
            padding: 5px 0;

            span {
                display: inline-block;
                width: 100%;
                text-align: center;
                font-weight: 500;
                color: $DarkColor;
            }
        }

        button {
            margin-top: 10px;
            @include Button;
            font-size: 1.2em;
            box-shadow: none;
        }
    }
}

.contact {
    width: 100%;
    padding: 80px 20px;
    display: flex;
    justify-content: center;

    .con {
        width: 28em;
        background-color: $MuColor;
        border-radius: $Radius;
        box-shadow: $Shadow;
        padding: 20px;
        .title{
            margin: 5px;
            text-align: center;
            h4{
                font-size: 1.1em;
                color: #111;
            }
            h2{
                font-size: 2.2em;
                color: $DarkColor;
                span{
                    color: $MColor;
                }
            }
        }
        form {
            input {
                width: 100%;
                padding: 10px;
                font-size: 1em;
                margin: 5px 0;
                background-color: #222;
                border: none;
                border-radius: $Radius;
                outline: none;
                color: $DarkColor;
            }

            textarea {
                width: 100%;
                padding: 10px;
                font-size: 1em;
                margin: 5px 0;
                background-color: #222;
                outline: none;
                border: none;
                border-radius: $Radius;
                color: $DarkColor;
                resize: none;
            }

            .but {
                width: 100%;
                text-align: center;
                button {
                    margin-top: 10px;
                    @include Button;
                    display: inline;
                    text-align: center;
                    padding: 10px 40px;
                    font-size: 1.1em;
                    box-shadow: none;
                }
            }

        }
    }
}