* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: system-ui;
}

body {
  background-color: #333;
  font-size: 16px;
  position: relative;
}
@media (max-width: 820px) {
  body {
    font-size: 18px;
  }
}

section {
  padding: 20px 50px;
}

.navbar {
  width: 100%;
  padding: 20px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  background-color: rgb(41, 41, 41);
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.377);
  position: relative;
}
@media (max-width: 570px) {
  .navbar {
    padding: 10px 50px;
  }
}
.navbar .logo {
  align-items: center;
}
.navbar .logo h2 {
  font-size: 1.6em;
  color: rgb(248, 248, 248);
  align-items: center;
}
.navbar .logo h2 span {
  color: rgb(225, 72, 45);
}
.navbar nav {
  display: flex;
  gap: 10px;
  align-items: center;
  align-content: center;
}
.navbar nav a {
  text-decoration: none;
  color: rgb(248, 248, 248);
  font-weight: 500;
  padding: 5px;
}
.navbar nav .Hr {
  background-color: rgb(225, 72, 45);
  border-radius: 10px;
}
@media (max-width: 570px) {
  .navbar nav {
    display: block;
    width: 10em;
    background-color: rgb(41, 41, 41);
    border-radius: 5px;
    position: fixed;
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.377);
    top: 60px;
    right: -100%;
    margin: 5px;
    padding: 5px;
    transition: 0.4s;
    transform-style: right;
  }
  .navbar nav a {
    width: 100%;
    display: inline-block;
    text-align: center;
  }
}
.navbar .show {
  right: 0;
}
.navbar .naShow {
  display: block;
  width: 30px;
  height: 30px;
  padding: 5px;
  background-color: #222;
  border-radius: 50%;
  border: 2px solid #444;
  display: none;
}
@media (max-width: 570px) {
  .navbar .naShow {
    display: block;
  }
}

footer {
  width: 100%;
  padding: 25px;
  text-align: center;
