  color: #e0e0e0;
}
footer h2 span {
  color: rgb(225, 72, 45);
  text-transform: uppercase;
}

.Home {
  background-size: cover;
  height: 80vh;
  transition: 0.5s;
  transform-style: background-image;
  margin-bottom: 80px;
}
.Home .content {
  padding: 150px 0;
}
.Home .content h2 {
  font-size: 2.3em;
  padding: 10px 0;
  color: rgb(225, 72, 45);
  font-weight: 700;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.377);
}
.Home .content p {
  font-size: 1.4em;
  padding: 5px 0;
  font-weight: 500;
  color: #e0e0e0;
  max-width: 20em;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.377);
}
.Home .content button {
  margin-top: 20px;
  font-size: 1em;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-weight: 400;
  color: #e0e0e0;
  cursor: pointer;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.377);
  position: relative;
  overflow: hidden;
  transition: 0.35s;
  transform-style: color;
  z-index: 2;
  font-size: 1.2em;
}
.Home .content button::before {
  content: "";
  position: absolute;
  top: 100%;
  right: -15%;
  width: 40px;
  height: 40px;
  transform: translateY(-50%);
  border-radius: 50%;
  background-color: #e0e0e0;
  transition: 0.4s;
  z-index: -1;
}
.Home .content button::after {
  content: "";
  position: absolute;
  top: 0%;
  right: 0%;
  width: 100%;
  height: 100%;
  background-color: rgb(225, 72, 45);
  transition: 0.4s;
  z-index: -2;
}
.Home .content button:hover {
  color: rgb(225, 72, 45);
}
.Home .content button:hover::before {
  content: "";
  position: absolute;
  top: 0%;
  right: 0%;
  width: 112%;
  height: 200%;
  border-radius: 0;
  background-color: #e0e0e0;
  transition: 0.4s;
}

